.main-allmembers-section {
    background-color: #150f22;
    padding-bottom: 120px;
}

.main-gradient {
    background-image: url("../../../../../assets/images/internalpages-bg.png");
    background-repeat: no-repeat;
    background-position: center;
    background-size: 100% 100%;
}

.heading-div {
    padding: 93px 0px;

    h1 {
        color: #fff;
        font-family: Urbanist;
        font-size: 48px;
        font-style: normal;
        font-weight: 600;
        line-height: normal;
    }

    p {
        color: #fff;
        font-family: Urbanist;
        font-size: 18px;
        font-style: normal;
        font-weight: 400;
        line-height: normal;
    }
}

.nav-btn {
    display: flex;
    width: 103px;
    padding: 10px;
    justify-content: center;
    align-items: center;
    gap: 10px;
    border-radius: 100px;
    border: 1px solid rgba(255, 255, 255, 0.10);
    color: #ffffff;
    background: #313131;
}

.nav-btn.active {
    background: var(--1, linear-gradient(130deg, #492FB2 -1.74%, rgba(73, 47, 178, 0.61) 125.81%));
    border: 1px solid var(--1, rgba(73, 47, 178, 0.61));
}

.bottom-margin {
    margin-bottom: 100px;
}

.div-color {
    color: #ffffff;
}

.news-video {
    max-width: 553px;
    max-height: 340px;
    border-radius: 16px;
    object-fit: cover;
    object-position: top;
    overflow: hidden;
}

.bg-image {
    position: relative;
    width: 100%;
    max-width: 553px;
    max-height: 340px;
    height: 100%;
    aspect-ratio: 553 / 340;
    background:
        linear-gradient(0deg, rgba(0, 0, 0, 0.50), rgba(0, 0, 0, 0.50)),
        url("../../../../../assets/images/caruna.png");
    background-size: cover;
    background-position: center;
    background-repeat: no-repeat;
    border-radius: 8px;
}

.svg-overlay {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    pointer-events: none;
}

.truncate {
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
    display: block;
    width: 90%;
}

.truncate-link {
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
    display: block;
    width: 70%;
}

.news-heading {
    overflow: hidden;
    text-overflow: ellipsis;
    font-size: 20px;
    font-weight: 400;
}

.common-text-style {
    font-style: normal;
    line-height: normal;
    text-transform: capitalize;
    font-family: 'Urbanist';
    color: #FFFFFF;
}

.news-second-heading {
    font-size: 32px;
    font-weight: 600;
}

.news-link {
    overflow: hidden;
    color: #FFF;
    text-overflow: ellipsis;
    white-space: nowrap;
    font-family: 'Urbanist';
    font-size: 16px;
    font-style: normal;
    font-weight: 400;
    line-height: normal;
    text-transform: lowercase;
}

.readmore-btn {
    display: flex;
    height: 48px;
    padding: 12px 24px;
    justify-content: center;
    align-items: center;
    gap: 8px;
    border-radius: 4px;
    border: 1px solid #FFF;
    background-color: transparent;
    color: #ffffff;
}

.news-card {
    border-radius: 24px;
    background: linear-gradient(148deg, rgba(88, 71, 114, 0.55) -33.59%, rgba(47, 42, 56, 0.55) 79.09%);
    color: #fff;
    overflow: hidden;
    max-width: 420px;
    padding: 16px;
}

.news-card img {
    width: 100%;
    max-height: 200px;
    object-fit: cover;
    object-position: top;
    border-radius: 16px;
}

.published-date {
    font-size: 14px;
    font-weight: 400;
    margin-bottom: 16px;
}

.card-title {
    font-size: 20px;
    font-weight: 600;
    margin-bottom: 16px;
    white-space: normal;
    overflow: visible;
    text-overflow: unset;
}

.text-truncate-2 {
    display: -webkit-box;
    -webkit-box-orient: vertical;
    overflow: hidden;
    font-size: 0.9rem;
    opacity: 0.85;
}

.link {
    font-size: 0.85rem;
    color: #bbb;
    text-decoration: none;
}

.link:hover {
    text-decoration: underline;
}

.margin-bottom-card {
    margin-bottom: 60px;
}

.text-truncate {
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
    width: 40%;
}

.card-top-margin {
    margin-top: 32px;
}

.card-text-news {
    font-size: 14px;
    font-weight: 400;
}

.second-readmore-btn {
    display: flex;
    height: 36px;
    padding: 12px 24px;
    justify-content: center;
    align-items: center;
    gap: 8px;
    border-radius: 4px;
    border: 1px solid #FFF;
    background-color: transparent;
    color: #ffffff;
}

.image-container {
    position: relative;
    width: 100%;
    max-width: 553px;
    max-height: 340px;
    aspect-ratio: 553 / 340;
    border-radius: 8px;
    overflow: hidden;
}

.image-container img {
    width: 100%;
    height: 100%;
    object-fit: cover;
    border-radius: 8px;
}

.read-more-btn {
  display: inline-flex;
  align-items: center;
  justify-content: center;
  gap: 6px;
  padding: 20px;
  height: 36px;
  line-height: 1;
  cursor: pointer;
  border: 1px solid #fff;
  border-radius: 6px;
  background: transparent;
  color: #fff;
  transition: background-color .25s ease, border-color .25s ease, color .25s ease;
}

.read-more-btn svg { margin-left: 6px; }
.read-more-btn svg path { fill: #fff; transition: fill .25s ease; }
.read-more-btn:hover {
   background: linear-gradient(
    130deg,
    #492FB2 -1.74%,
    rgba(73, 47, 178, 0.61) 125.81%
  );
  border-color: transparent;
  color: #fff;
}
.read-more-btn:hover svg path { fill: #fff; }


.left-div {
    display: flex;
    flex-direction: column;
    align-items: start;
    justify-content: space-between;
}

.card-read-more{
    color: #fff;
    cursor: pointer;
    transition: background-color .25s ease, border-color .25s ease, color .25s ease;
    background: transparent;
}

.card-read-more svg { margin-left: 6px; }
.card-read-more svg path { fill: #fff; transition: fill .25s ease; }
.card-read-more:hover {
   background: linear-gradient(
    130deg,
    #492FB2 -1.74%,
    rgba(73, 47, 178, 0.61) 125.81%
  );

  border-color: transparent;
  color: #fff;
}
.card-read-more:hover svg path { fill: #fff; }

